@import url(https://fonts.googleapis.com/css?family=Staatliches:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Thasadith:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.home-page {
    font-family: Staatliches;
    background-color: #a2aab0;
    min-height: 100vh;
    width: 100vw;
}

.home-header {
    display: flex;
    height: 70vh;
    justify-content: center;
    align-items: center;
}

.name {
    color: white;
    font-family: Staatliches;
    font-style: normal;
    font-weight: normal;
    font-size: 200px;
    line-height: 250px;
    text-align: center;
}

.title {
    color: #4C586F;
    font-family: Thasadith;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    font-weight: 700;
}

@media only screen and (max-width: 815px) {

    .home-header {
        height: 60vh;
    }

    .name {
        line-height: 100px;
        font-size: 100px;
        margin: 8px 48px;
    }

    .title {
        font-size: 18px;
    }
}
.navbar{
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 24px;
    padding: 32px 0;
    font-family: Staatliches;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
}

.menu__list {
    list-style-type: none;
    padding: 0;
    height: 100%;
    margin: 0;
}

.menu__list-item {
    display: inline-block;
    height: 100%;
    margin-right: 20px;
}

.menu__list-item:last-child {
    margin-right: 0;
}

.menu__link {
    color: #3a3e47;
    display: inline-block;
    height: 100%;
    text-decoration: none;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    padding: 0 3px;
    transition: all 0.10s ease-in-out
}

.menu__link:hover {
    color:white;
    transform: scale(1.1);
}

.menu__link--active {
    color: white;
    border-bottom: 4px solid white;
    font-weight: 700;
}
.footer {
    margin-top: 48px;
    width: 100%;
}

span.footer-info {
    display: flex;
    justify-content: center;
    font-family: Thasadith;
    font-weight: 700;
    font-size: 18px;
    color: #4C586F
}

hr {
    width: 30%;
    color:black; 
}

ul.Social-Icons {
    display: flex;
    width: 100%;
    justify-content: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .icon {
    font-size: 24px;
    color: #4C586F;
    border-radius: 50%;
    padding: 8px;
    margin: 8px 8px;
    transition: all .2s ease-in-out;
  }
  
  :hover.icon {
    cursor: pointer;
    transform: scale(1.2);
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
    display: inline-block;
    position:absolute;
    bottom: -50%;
    left:50%;
    transform:translateX(-50%);
    text-align: center;
  }

  .tooltiptext {
    color: white;
    background-color: #4C586F;;
    visibility: hidden;
    font-family: Roboto;
    position: absolute;
    font-size: 12px;
    padding: 4px 8px;
    border-radius: 10%;
  }

  footer {
    display: flex;
    justify-content: center;
    font-size: 14px;
    padding-bottom: 24px;
    font-family: Roboto;
    color: #4C586F
  }

  @media only screen and (max-width: 815px) {
    span.footer-info {
      margin: 0 24px;
      text-align: center;
      font-size: 14px;
  }
}
.about-page {
    font-family: Staatliches;
    background-color: #EBECED;    
    width: 100vw;
    min-height: 100vh;
    color: #3E3E3B;
}

.infos {
    display: flex;
}

.headshot {
    height: 65vh;
    margin: 12px 50px 0 225px;
    border-radius: 10px;
}
.example-appear {
    opacity: 0.01;
}
  
.example-appear.example-appear-active {
    opacity: 1;
    transition: opacity 1s ease-in;
}

.about-info {
    margin: 8px 225px 0 0;
}

.intro {
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 16px;
}

.paragraph {
    font-family: Thasadith;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    font-weight: 700;
    margin-bottom: 20px;
}


@media only screen and (max-width: 815px) {

    .infos {
        display: flex;
        flex-direction: column;
    }

    .headshot {
        display: block;
        max-width: 85vw;
        height: auto;
        margin: auto;
    }

    .about-info {
        margin: 24px;
    }
}
